	var AboutMe = exports.AboutMe = 'During my undergraduate studies I worked on a behavioral health inpatient unit to explore my interests in clinical psychology. My responsibilities were to check vitals, assist with activities of daily living (ADLs), and monitor safety. However, the insight I gained was far beyond the scope of these tasks. One instance a patient was placed in a safe room for being a potential threat to himself or others. On my rounds I noticed he seemed frightened and was unwilling to answer any questions. Instead of continuing my rounds, I handed him a piece of paper and pencil. Immediately he started communicating with me through this medium. It turned out he was experiencing psychosis and was unaware of his location. For the rest of his stay he felt comfortable asking me for help because I took the time to understand what he was experiencing. It became clear to me that although institutions may be attempting to support individuals with severe mental illness (SMI), they may not be implementing the best practices to do so. I continued to work in clinical settings, which brought up many questions regarding my patients illnesses. For example, what neurological mechanisms underly psychosis symptoms? What is the system-level effect of antipsychotic medication? What factors contribute to treatment response and can we predict these outcomes? I believe careful research could elucidate these questions to advance assessment and treatment for those experiencing SMI.';
	
